.product {
  display: flex;
  align-items: center;
  flex-direction: column;
  position: relative;
  height: 600px;

  @include media-breakpoint-down(xs) {
    flex-direction: column;
    height: auto;
  }

  &-link {
    font: normal 12px/16px $font-ubuntu;
    color: $primary;
    padding-bottom: 2px;
    border-bottom: 1px dashed $primary;
    margin-bottom: 5px;

    &:hover, &:focus {
      color: $primary-second;
      border-color: $primary-second;
    }
  }

  &-photos {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: absolute;
    left: 0;
    width: 66.6%;

    @include media-breakpoint-down(md) {
      width: 56.6%;
    }

    @include media-breakpoint-down(xs) {
      width: 100%;
      position: relative;
      order:2;
    }
    .product-zoom {
      display: flex;
      height: 500px;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      @include media-breakpoint-down(xs) {
        height: 50vh;
        display: none;
      }

      &:before {
        content: '';
        background-image: url(#{$PATH}/svg/zoom.svg);
        display: block;
        position: absolute;
        width: 30px;
        height: 30px;
        z-index: 1010;
        opacity: .05;
        bottom: 170px;
        right: 27px;
      }
      .zoom-img {
        height: 500px;
        margin: 0 auto;
        z-index: 0;

        @include media-breakpoint-down(xs) {
          height: 50vh;
        }
      }
    }

    .product-slide {
      &-item {
        display: flex;
        position: relative;

        .zoom-img {
          width: auto;
          height: 500px;
          margin: 0 auto;
          z-index: 3;

          @include media-breakpoint-down(xs) {
            height: 50vh;
          }
        }
      }
    }
  }

  &-brand {
    //border: 1px solid #f5f9fe;
    display: inline-block;
    //padding: 15px;
    margin-bottom: 10px;
    position: relative;
    display: flex;
    align-items: center;

    > div {
      border-left: 1px solid #f4f9fe;
      padding-left: 20px;
      margin-left: 10px;
      color: #666;
      font-size: 13px;
      line-height: 18px;

      p {
        font: normal 13px/18px $font-ubuntu;

        a {
          margin-left: 5px;
        }
      }
    }
  }

  &-rating {
    display: flex;
    align-items: center;
    margin-bottom: 5px;
    margin-left: auto;
    width: 23.2%;

    @include media-breakpoint-down(md) {
      width: 33.2%;
    }
    
    @include media-breakpoint-down(xs) {
      width: 100%;
      order: 1;
    }

    svg {
      fill: #01b2ff;
      width: 15px;
      height: 15px;
    }

    a {
      border-bottom: 1px solid #77777778;
      color: #777;
      margin-left: 8px;
      font-size: 11px;
      padding-bottom: 0px;
      line-height: 11px;
    }
  }

  &-title {
    font-size: 18px;
    line-height: normal;
    color: #333333;
    display: flex;
    align-items: center;
    font-weight: 300;
    flex-wrap: wrap;
    margin-bottom: 5px;
    margin-left: auto;
    width: 23.2%;

    @include media-breakpoint-down(md) {
      width: 33.2%;
    }

    @include media-breakpoint-down(xs) {
      width: 100%;
      order: 1;
    }

    span {
      font-weight: 400;
      border-bottom: 1px solid #333;
      margin-left: 4px;
    }

    img {
      display: inline-block;
      max-width: 80px;
      transform: translateY(-10px);
    }
  }

  &-info {
    position: relative;
  }

  &-articul {
    font: 400 12px/18px $font-ubuntu;
    color: #999;
    line-height: 18px;
    display: inline-block;
    margin-bottom: 20px;
    margin-left: auto;
    width: 23.2%;

    @include media-breakpoint-down(md) {
      width: 33.2%;
    }

    @include media-breakpoint-down(xs) {
      width: 100%;
      order: 1;
      margin-bottom: 10px;
    
      .modal {
        margin-bottom: 0;
      }
    }

    span {
      color: #666666;
      margin-left: 6px;
    }
  }
    
  &-price {
    display: flex;
    align-items: baseline;
    flex-wrap: wrap;
    margin-bottom: 10px;
    margin-left: auto;
    width: 23.2%;

    @include media-breakpoint-down(md) {
      width: 33.2%;
    }

    @include media-breakpoint-down(xs) {
      width: 100%;
      margin: 0 0 10px 0;
      order: 3;

      .modal & {
        margin-top: 20px;
        margin-bottom: 20px;
      }
    }

    .price {
      position: relative;
      display: flex;
      align-items: baseline;
      line-height: 30px;
      font-size: 30px;
      color: #333;

      &--sale {
        font-size: 20px;
        font-weight: 300;
        margin-right: 10px;
        color: #bfc7c7;

        span {
          position: relative;

          &:after {
            content: '';
            display: block;
            border-top: 1px solid;
            position: absolute;
            width: 100%;
            top: 49%;
          }

          small {
            font-size: 0.8em;
            margin-left: 2px;
          }
        }

        .price-badge-discount {
          display: inline-block;
          color: #fff;
          height: 22px;
          line-height: 22px;
          border-radius: 4px;
          position: relative;
          padding: 0 5px;
          margin-left: 8px;
          font-size: 12px;
          font-family: Ubuntu;
          top: -7px;
          background-color: #f69781;

          &:before {
            background-color: #f69781;
          }

          &:after {
            display: none;
          }
        }
      }

      &--total {
        margin: 0 0 0 20px;

        @include media-breakpoint-up(sm) {
          width: 100%;
          margin: 5px 0 0 0;   
        }
        
        small {
          font-size: 23px;
          padding-left: 3px;
        }
      }
    }
  }

  &-promo {
    position: relative;
    margin-bottom: 20px;
    margin-left: auto;
    width: 23.2%;

    @include media-breakpoint-down(md) {
      width: 33.2%;
    }

    @include media-breakpoint-down(xs) {
      width: 100%;
      order: 3;
    }

    &-title {
      display: inline-block;
      height: 27px;
      font: normal 12px/26px $font-ubuntu;
      color: white;
      padding: 0 10px 0 4px;
      text-align: center;
      background: #ff423c;
      border-radius: 0 4px 4px 0;
      margin-bottom: 10px;

      &:before {
        content: '';
        width: 19px;
        background-color: #ff423c;
        height: 19px;
        display: inline-block;
        position: absolute;
        left: -10px;
        top: 4px;
        border-top-left-radius: 6px;
        transform: rotate(-45deg);
        z-index: -1;
      }

      a {
        border-bottom: 1px dashed;
      }
    }

    &-content {
      display: none;
      flex-direction: column;
      position: absolute;
      align-items: center;
      z-index: 1010;
      right: 100%;
      top: 0;
      background: white;
      padding: 20px 30px;
      border: 1px solid $border-color;
      margin-right: 20px;

      .cpipc {
        &-price {
          font-size: 25px;
          line-height: 25px;
          margin-bottom: 10px;
          color: #333;

          small {
            font-size: 20px;
            margin-left: 4px;
          }
        }
        &-close {
          background: url(#{$PATH}/svg/close.svg);
          background-size: contain;
          background-repeat: no-repeat;
          background-position: center;
          position: absolute;
          min-width: 10px;
          max-width: 10px;
          top: 10px;
          right: 10px;
          height: 10px;
          cursor: pointer;
          border: none;
        }
        &-text {
          font-size: 13px;
          color: #333333;
          line-height: 18px;
          text-align: center;
          margin: 0px 0 20px 0;

          span {
            font-weight: 700;
          }
        }
        &-apply {
          display: flex;
          background: $primary;
          font-size: 13px;
          color: white;
          padding: 10px 28px;
          border: 1px solid transparent;
          cursor: pointer;
          margin-bottom: 10px;
          
          &:hover, &:focus {
            background: $primary-second;
          }
        }
        &-timer {
          display: flex;
          align-items: center;
          color: #ff423c;
          font-size: 12px;
          line-height: 16px;
          margin-bottom: 10px;
          
          svg {
            width: 16px;
            height: 16px;
            margin-right: 5px;
            fill: #ff423c;
          }
        }
      }
    }

    &.active {
      .product-info-promo-content {
        display: flex;

        @include media-breakpoint-down(xs) {
          right: 0;
          margin: 0;
          left: 0;
          top: 37px;
        }
      }
    }
  }

  &-credit {
    font: normal 12px/18px $font-ubuntu;
    color: #333333;
    margin-left: auto;

    span {
      color: $primary;
      margin-left: 3px;
      margin-right: 3px;
    }
    a {
      margin-left: 3px;
    }
  }

  &-options {
    margin-bottom: 20px;
    margin-left: auto;
    width: 23.2%;

    @include media-breakpoint-down(md) {
      width: 33.2%;
    }

    @include media-breakpoint-down(xs) {
      width: 100%;
      order: 2;
      margin-bottom: 0;
    }

    > div {
      display: flex;
      margin-bottom: 10px;
    }

    &-title {
      font: 400 12px/18px $font-ubuntu;
      color: rgba(51, 51, 51, 0.5);
      margin-bottom: 5px;
      display: inline-block;
    }

    &-item {
      margin-right: 5px;
      border: 1px solid transparent;
      padding: 0 5px;
      cursor: pointer;

      img {
        width: auto;
        height: 90px;

        @include media-breakpoint-down(md){
          height: 65px;
        }
      }

      &.active {
        border: 1px solid $border-color;
      }
    }
  }

  &-size {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-left: auto;
    width: 23.2%;

    @include media-breakpoint-down(md) {
      width: 33.2%;
    }

    @include media-breakpoint-down(xs) {
      width: 100%;
      order: 4;
      margin-bottom: 20px;
    }
    
    &-title {
      display: flex;
      align-items: baseline;
      width: 100%;
      font: 400 12px/18px $font-ubuntu;
      color: #999;
      margin-bottom: 5px;

      a {
        margin-left: auto;
      }
    }
    
    > div {
      display: flex;
      flex-wrap: wrap;
      margin: 0 -4px 10px -4px;
    }

    &-item {
      border: 1px solid $border-color;
      font: normal 12px/30px $font-ubuntu;
      cursor: pointer;
      color: #333333;
      padding: 0;
      margin: 3px;
      height: 35px;
      width: 35px;
      text-align: center;
      
      &:hover, &:focus {
        border-color: $primary-second;
        color: $primary-second;
      }
      &.active {
        border: 1px solid $primary-second;
        color: white;
        background: $primary-second;
      }
    }

    &-check {
      display: flex;
      position: relative;
      margin-bottom: 15px;
      align-items: center;
      font-size: 12px;
      color: #666666;
      letter-spacing: 1px;
      padding-bottom: 3px;
      border-bottom: 1px dashed #666666;
      
      img {
        width: 17px;
        height: 17px;
        border: 1px solid #dadbdb;
        border-radius: 50%;
        padding: 3px;
        margin-right: 5px;
      }

      a {
        font-size: 12px;
        color: $primary;
        line-height: 25px;
        margin-left: auto;
      }
    }
  }

  &--preview {

    .product-info-name-brand {
      padding-bottom: 10px;
      margin-top: 20px;
      border-bottom: 1px solid #e9eff7;
    }
    .product-info-brand {
      margin-left: auto;
    }
    .product-feature-list {
      flex-direction: column;
      margin: 20px 0;
    }
    .product-feature-item {
      width: 100%;
    }
    .product-feature-name {
      min-width: auto;
      margin: 0;
      white-space: nowrap;
    }
    .product-rating {
      font-family: "Roboto Condensed", sans-serif;
      font-size: 15px;
      text-transform: uppercase;
      line-height: 16px;
      color: #9e9e9e;
      
      span {
        margin-left: 5px;
      }

      i {
        color: #fed545;
      }
    }
    .product-link {
      margin-right: 10px;

      svg {
        fill: $primary;
        width: 20px;
        height: 20px;
        transform: rotate(-180deg);
        margin-left: -5px;
        margin-right: -20px;
      }

      &:hover, &:focus {
        svg {
          fill: $primary-second;
        }
      }
    }
  }

  &-add {
    display: flex;
    margin-left: auto;
    width: 23.2%;
    margin-top: auto;

    @include media-breakpoint-down(md) {
      width: 33.2%;
    }

    @include media-breakpoint-down(xs) {
      width: 100%;
      order: 5;
    }

    a {
      display: flex;
      align-items: center;
      justify-content: center;
      transition: .4s;
      text-transform: uppercase;
      font: normal 12px/40px $font-ubuntu;
    }

    &-to-cart {
      background: $primary;
      color: white;
      height: 40px;
      padding: 0 10px;
      text-transform: uppercase;
      border: 1px solid transparent;

      svg {
        width: 25px;
        height: 25px;
        margin-right: 5px;
        fill: #fff;
      }

      &:hover, &:focus {
        background: $primary-second;
        color: #fff;
      }
    }

    

    &-to-like {
      border: 2px solid #dadbdb;
      margin-left: 10px;
      width: 40px;
      min-width: 40px;

      @include media-breakpoint-down(md) {
        width: 40px;
        height: 40px;
      }

      span {
        display: none;
        @include media-breakpoint-down(md) {
          display: block;
        }
      }

      svg {
        width: 28px;
        height: 30px;
        fill: $primary-second;

        @include media-breakpoint-down(md) {
          
        }
      }
      
      &:hover, &:focus {
        background-color: $primary-second;
        border-color: $primary-second;

        svg {
          fill: #fff;
        }
      }
    }
  }

  &-headline {
    font-size: 22px;
    line-height: 1.5;
    margin: 30px 0 20px;
    width: 66.6%;

    @include media-breakpoint-down(md) {
      width: 100%;
    }
    @include media-breakpoint-down(xs) {
      margin: 20px 0 10px;
    }
  }

  &-info-text {
    font-size: 16px;
    line-height: 1.5;
    font-weight: 300;
    width: 66.6%;

    @include media-breakpoint-down(md) {
      width: 100%;
    }

    @include media-breakpoint-down(xs) {
      width: 100%;
    }
  }

  &-text {
    font-size: 16px;
    line-height: 1.5;
    font-weight: 300;
  }
  
  &-feature {
    
    &-list {
      display: flex;
      flex-wrap: wrap;
      width: 100%;
      margin: 0;
      justify-content: space-between;
      width: 66.6%;

      @include media-breakpoint-down(md) {
        width: 100%;
      }

      @include media-breakpoint-down(xs) {
        width: 100%;
        margin-bottom: 0;
      }
    }
    &-item {
      display: flex;
      align-items: flex-start;
      width: 47%;
      padding: 0 0 15px 0;

      @include media-breakpoint-down(xs) {
        width: 100%;
      }
    }

    &-name-trait {
      font-size: 14px;
      font-family: $font-ubuntu;
      color: #777;
      white-space: nowrap;
      
      @include media-breakpoint-down(xs) {
        white-space: initial;
      }
    }
    &-hr-trait {
      margin: 12px 3px auto 3px;
      width: 100%;
      border: none;
      border-bottom: 1px dotted #e0ebf3;
    }
    &-name {
      color: #000;
      font: normal 14px/21px $font-ubuntu;
      white-space: nowrap;
      text-align: right;

      span {
        display: block;
      }
    }

    &-logo {
      min-width: 200px;
      max-width: 200px;
      text-align: center;
      padding: 20px;
      border: 1px solid #e9eff7;
      margin: 40px 40px 30px 0;

      @include media-breakpoint-down(xs) {
        margin: 10px 0px 0px 0px;
      }
    }

    &-description {
      display: flex;
      align-items: flex-start;
      width: 66.6%;

      @include media-breakpoint-down(md) {
        width: 100%;
      }

      @include media-breakpoint-down(xs) {
        width: 100%;
        flex-direction: column;
      }
    }
  }

  &-reviews {
    width: 66.6%;

    @include media-breakpoint-down(md) {
      width: 100%;
    }

    @include media-breakpoint-down(xs) {
      margin: 20px 0;
      width: 100%;
    }

    &-header {
      display: flex;
      align-items: center;
      margin: 40px 0 20px;
    }

    &-total {
      color: #fff;
      font-size: 20px;
      position: relative;
      height: 50px;
      width: 50px;
      line-height: 50px;
      text-align: center;
      letter-spacing: -1px;
      font-weight: 300;

      svg {
        position: absolute;
        width: 50px;
        height: 50px;
        fill: #00b2ff;
        top: -2px;
        left: 0;
        z-index: -1;
      }
    }
    &-title {
      font-size: 22px;
      line-height: 1.5;
      margin: 0 0 0 20px;
    }
    .btn {
      margin-left: auto;
    }
  }

  &-delivery {
    display: flex;
    flex-direction: column;
    align-content: center;
    border: 1px solid $border-color;
    padding: 35px 40px;
    box-shadow: 0 0 20px -15px #0000001f;
    margin-top: 30px;
    position: absolute;
    width: 23.2%;
    right: 0;

    @include media-breakpoint-down(md) {
      margin-top: 20px;
      flex-direction: row;
      align-items: center;
      padding: 30px;
      flex-wrap: wrap;
      margin-bottom: 15px;
      width: 100%;
      position: relative;
    }

    &-title {
      font-size: 18px;
      margin-bottom: 15px;
      
      @include media-breakpoint-down(md) {
        margin-bottom: 0;
        margin-right: 10px;
      }
    }

    &-city {
      display: flex;
      align-items: center;
      
      svg {
        height: 20px;
        width: 20px;
        fill: $primary;
        margin-right: 2px;
        margin-left: -5px;
      }
      span {
        color: $primary;
        border-bottom: 1px dashed $primary;
      }

      &:hover, &:focus {
        svg {
          fill: $primary-second;
        }
        span {
          color: $primary-second;
          border-bottom-color: $primary-second;
        }
      }
    }

    &-type {
      display: flex;
      flex-direction: column;

      @include media-breakpoint-down(md) {
        flex-direction: row;
        justify-content: space-between;
        width: 100%;
      }

      &-item {
        margin-top: 15px;
        
        @include media-breakpoint-down(md) {
          margin-top: 20px;
        }

        .cpdti {

          &-title {
            font: normal 14px/16px $font-roboto;
            color: #333;
            margin-bottom: 5px;
            display: inline-block;

            span {
              color: $primary;
              margin-left: 4px;
            }
          }
          &-date {
            font-size: 13px;
            line-height: 19px;
            color: #777;
            display: flex;
            align-items: center;
          }
          &-point {
            font-size: 13px;
            line-height: 19px;
            color: #777;
            display: flex;
            align-items: center;
          }
          &-option {
            font-size: 13px;
            line-height: 19px;
            color: #777;
            display: flex;
            align-items: center;

            &:before {
              content: '';
              width: 10px;
              height: 10px;
              margin-right: 7px;
              background-size: 100%;
            }

            &.option-yes {
              &:before {
                background-image: url(#{$PATH}/svg/lothes-fitting-yes.svg);
              }
            }
  
            &.option-no {
              &:before {
                background-image: url(#{$PATH}/svg/lothes-fitting-no.svg);
              }
            }
            
            &.option-warning {
              &:before {
                height: 12px;
                width: 14px;
                background-image: url(#{$PATH}/svg/lothes-fitting-warning.svg)
              }
            }
          }
        }
      }
    }

    &-free {
      margin-top: 15px;
      
      @include media-breakpoint-down(md) {
        margin-top: 20px;
      }

      p {
        font: normal 14px/16px $font-roboto;
        color: #333;
        margin-bottom: 5px;

        span {
          color: $primary;
          text-transform: uppercase;
          margin-right: 5px;
        }
      }

      a {
        font: normal 12px/14px $font-ubuntu;
        color: #333;
        margin-top: 10px;
        padding-bottom: 2px;
        border-bottom: 1px dashed #333;
        display: inline-block;

        &:hover, &:focus {
          color: $primary-second;
          border-color: $primary-second;
        }
      }
    }
  }

  &-additional {
    display: flex;
    flex-direction: column;
    margin-top: 30px;
    box-shadow: 0 0 20px -15px #0000001f;
    position: absolute;
    width: 23.2%;
    right: 0;
    bottom: 0;

    @include media-breakpoint-down(md) {
      position: relative;
      width: 100%;
      flex-direction: row;
      margin-top: -1px;
    }

    @include media-breakpoint-down(xs) {
      flex-direction: column;
      display: none;
    }
  }

  &-payment, &-fitting, &-return {
    display: flex;
    flex-direction: column;
    align-content: center;
    border: 1px solid $border-color;
    padding: 25px 30px;
    text-align: center;
    margin-bottom: -1px;
    width: 100%;

    img {
      margin-bottom: 20px;
    }
    p {
      font: normal 14px/16px $font-ubuntu;
      color: #333333;
    }
  }
  
  &-fitting {
    @include media-breakpoint-down(md) {
      border-left: none;
      border-right: none;
    }
    @include media-breakpoint-down(xs) {
      border: 1px solid #eaeff7
    }
  }

  &-tags {
    max-width: 100%;
    padding: 0;
    margin: 30px auto;
    
    @include media-breakpoint-down(xs) {
      margin: 0 auto;
      display: none;
    }

    .tags-slider {
      align-items: center;
      flex-wrap: nowrap;
      display: flex;
      overflow-x: auto;
      padding-bottom: 15px;
    }
    
    .tag-item {
      display: inline-block;
      position: relative;
      font-size: 11px;
      letter-spacing: 0.7px;
      line-height: 30px;
      color: #778190;
      height: 30px;
      border-radius: 15px;
      text-align: center;
      padding: 0 12px;
      background: white;
      text-transform: uppercase;
      border: 1px solid #e9eff7;
      margin: 0;
      transition: .4s;
      white-space: nowrap;

      @include media-breakpoint-down(xs) {
        margin-right: 5px;
      }
      
      &:hover, &:focus {
        border-color: #00b3ff;
        color: #00b3ff;
      }
    }
  }
}



.interesting-filters {
  margin: -20px auto 40px;
  display: flex;
  justify-content: center;

  @include media-breakpoint-down(xs) {
    margin: 0 0 30px 0;
  }

  &-link {
    font-size: 15px;
    text-transform: uppercase;
    line-height: 16px;
    color: #9e9e9e;
    letter-spacing: 1px;
    margin: 0 5px;
    padding: 0 10px 8px 10px;
    border-bottom: 2px solid transparent;

    &:hover, &:focus, &.active {
      color: #333333;
      border-color: transparent;
    }
    &.active {
      border-bottom: 2px solid $primary;
    }
  }
}

.zoomContainer {
  border: 1px solid transparent;
  border-bottom: none;

  &:hover, &:focus {
    border-color: #f5f9fe;
  }
}