.banner {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  background-color: #f5f9fe;
  background-position: center;
  background-size: cover;
  margin-bottom: 30px;

  @include media-breakpoint-down(md) {

  }

  @include media-breakpoint-down(sm) {
    
  }
  
  @include media-breakpoint-down(xs) {

  }


  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: 10;
    background: #000;
    opacity: 0;
    transition: .4s;
    cursor: pointer;
  }

  &:hover, &:focus {
    &:before {
      opacity: .2;
    }
  }

  &--big {
    height: 600px;

    @include media-breakpoint-down(md) {
      height: 30vh;
    }
  }
  &--small {
    height: 285px;

    @include media-breakpoint-down(md) {
      height: calc(15vh - 15px);
    }
    @include media-breakpoint-down(xs) {
      height: 30vh;
    }
  }
  &--horizontal {
    height: 285px;
 
    .banner-text {
      @include media-breakpoint-up(lg) {
        width: 40%;
      }
    }
  }
  &--vertical {
    height: 600px;
  }


  img {
    
    height: 100%;
  }

  &-button {
    display: none;
    background: white;
    margin: auto;
    font-size: 14px;
    width: 175px;
    font-family: "Ubuntu", sans-serif;
    text-transform: lowercase;
    height: 44px;
    line-height: 44px;
    text-align: center;
    z-index: 11;
  }

  &:hover, &:focus {
    .banner-button {
      display: block;
    }
  }

  &-text {
    padding: 0;
    width: 75%;
    display: flex;
    align-items: center;
    height: 70px;
    justify-content: center;
    flex-direction: column;
    background-color: #fff;
    margin: 0 auto -40px auto;
    z-index: 1010;
    box-shadow: 0 -7px 14px -10px #0000001c;

    @include media-breakpoint-down(xs) {
      width: auto;
      margin-left: 30px;
      margin-right: 30px;
    }
    
    span:first-child {
      font-size: 16px;
      font-weight: 600;
      line-height: 30px;
      color: #333;
      text-transform: uppercase;
    }

    span:last-child {
      font-size: 15px;
      font-weight: 300;
    }

    &-badge {
      padding: 10px;
      height: 45px;
      position: relative;
      width: 120px;
      align-self: flex-end;
      margin-top: 20px;
      
      &:before {
        content: '';
        position: absolute;
        width: 0;
        height: 0;
        border-left: 15px solid transparent;
        top: 0;
        left: -15px;
      }

      span {
        color: #fff;

        &:first-child {
          font-size: 20px;
          line-height: normal;
          font-weight: 500;
        }
        &:last-child {
          font-size: 20px;
          font-weight: 300;
          text-transform: uppercase;
          padding-left: 7px;
        }
      }

      &--discount {
        background-color: #e5322d;

        &:before {
          border-top: 45px solid #e5322d;
        }
      }

      &--brand {
        background-color: $primary-second;

        &:before {
          border-top: 45px solid $primary-second;
        }
      }
    }
  }
}